import { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useGetClientsQuery, selectClientById, selectClientsResult, selectAllClients, selectClientIds, useUpdateClientMutation } from '../../../features/admins/clientsApiSlice';
import { useGetAdminsQuery, selectAdminById, selectAdminsResult, selectAllAdmins, selectAdminIds } from '../../../features/admins/adminsApiSlice';
import useAuth from '../../../hooks/useAuth';

const ClientAccount = () => {
  const { id } = useParams();
  const you = useAuth();
  const client = useSelector(state => selectClientById(state, id));
  const admins = useSelector(state => selectAllAdmins(state));
  const [trader, setTrader] = useState("");
  let {
    firstname,
    lastname,
    email,
    phone,
    createdAt,
    updatedAt,
  } = {}; 

  const [
    updateClient, {
    isLoading,
    isError,
    isUninitialized,
    isSuccess
    }
]= useUpdateClientMutation();

  useEffect(() => {
    setTrader(client.trader);
  }, [client]);

  console.log(trader, client.trader)
  const newDataReady = (trader && ((client && !client.trader) || (client && client.trader && (client.trader !== trader)))) ;

  //const {firstname, email} = useSelector(state => selectClientById(state, id));

  //var
  //  firstname = client?.firstname, 
  //  lastname = client?.lastname, 
  //  phone = client?.phone, 
  //  email = client?.email, 
  //  createdAt = client?.createdAt,
  //  updatedAt = client?.updatedAt;


  const handleSetTrader = e => {
    console.log(e.target.value);
    setTrader(e.target.value);
  }

  const handleUpateClient = e => {
    e.preventDefault();
    updateClient({...client, trader})
  }
  console.log(isError);

  return (
  <div className="container ">
    <div className="row">
      <div className="col-lg-12 container">
        { client &&  
        <div className="row">
          <div className="col-lg-8 container">
            <div className="row">
              <div className="col-lg-4 border border-dark overflow-hidden">
                <div className="col-lg-12 text-dark h5">{(client.firstname || client.lastname) ? [client.firstname, client.lastname].join(" ") : client.email }</div>
              </div>
              <div className="col-lg-4 border border-dark">
                <div className="col-lg-12 text-dark h5">депозит</div>
              </div>
              <div className="col-lg-4 container border border-dark">
                <div className="row">
                  <div className="col-lg-6 text-dark h5">создан:  </div>
                  <div className="col-lg-6 text-dark">{createdAt ? new Date(createdAt).toLocaleDateString("ru-RU") : "нет даных"}</div>
                </div>
              </div>

              <div className="col-lg-8 border border-dark">KOSH</div>
              <div className="container border border-dark col-lg-4">
                <div className="row">
                  <div className="col-lg-6 text-dark h5">обновлён:  </div>
                  <div className="col-lg-6 text-dark">{updatedAt ? new Date(createdAt).toLocaleDateString("ru-RU") : "нет даных"}</div>
                </div>
              </div>
              
              <div className="bg-primary col-lg-12"><pre>{"graph\ngraph\ngraph\ngraph\ngraph\ngraph\ngraph\ngraph\ngraph\n"}</pre></div>
              <div className="bg-warning col-lg-12">btns</div>
            </div>
          </div>
          <div className="col-lg-4 container">
            <div className="row">
              <span className="h5">ID: {id}</span>
              <div className="col-lg-6 text-dark h5">телефон:  </div>
              <div className="col-lg-6 text-dark">{phone}</div>

              <div className="col-lg-6 text-dark h5">почта: </div>
              <div className="col-lg-6 text-dark">{email}</div>

              <div className="col-lg-3">
                <div className="text-dark h5">трейдер: </div>
              </div>
              <div className="col-lg-9">
                <select onChange={handleSetTrader} value={client.trader} className="form-select" aria-label="назначить тредера">
                  <option value={""}>назначить трейдера</option>
                  {
                    admins.map((admin, i) => 
                      <option 
                        key={`option${admin.username}`}
                        style={{border: '1px solid red'}}
                        className={(admin.username === you.username ? 'text-primary' : '') + (i % 2 == 0 && ' bg-secondary')} 
                        value={admin.username}
                      >
                        {admin.username} {admin.username === you.username ? 'вы' : ''}
                      </option>)
                  }
                </select>
              </div>
              <div className="col-lg-12">
                <button onClick={handleUpateClient} className="btn btn-primary" disabled={!newDataReady}>обновить</button>
              </div>
            </div>
          </div>
        </div>
          }
      </div>
      <Link to="/admin/home">Назад к списку клентов</Link>
    </div>
  </div>
  );
}

export default ClientAccount;

